.form {
    display: flex;
    flex-direction: column;
    border: 2px solid black;
    border-radius: 4px;
    width: 320px;
    padding: 10px;
}

.button {
    width: 100px;
    cursor: pointer;
}

.button:hover,
.button:focus {
    box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.8);
}

.label {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin-bottom: 15px;
}